<template>
  <div class="welcome" v-show="isLogin">
    生日礼物代码
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'Welcome',
  data () {
    return {
      isLogin: false
    }
  },
  mounted: function () {
    this.dialog()
  },
  methods:{
    dialog(){
      MessageBox.prompt('苏玉洁的男朋友名字?', '', {
        showInput: true,
        closeOnClickModal: false,
        showCancelButton: false,
        inputValidator: (val) => {
          if (val === '') {
            return false;//初始化的值为null，不做处理的话，刚打开MessageBox就会校验出错，影响用户体验
          }
          return (val=='李毅')
        },
        inputErrorMessage: '回答错误'
      }).then(({ value, action }) => {
        let instance = Toast({
          message: '回答正确',
          position: 'middle'
        });
        setTimeout(() => {
          this.isLogin = true;
          instance.close();
        }, 1000);
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



